import { instance } from 'api/instance'

export const cardsAPI = {
  getCards(params?: CardsParamsType) {
    return instance.get<CardsResponceType>('/cards/card', { params })
  },
  postCards(data: NewCardType) {
    return instance.post<CardsResponceType>('/cards/card', data)
  },
  deleteCard() {
    return instance.delete('/cards/card', {
      params: {
        id: '6435accee26fcaa528bf1717',
      },
    })
  },
  updateCard(data: UpdateCardType) {
    return instance.put('cards/card', data)
  },
}

export type CardsParamsType = {
  cardAnswer?: string //  
  cardQuestion?: string //  
  cardsPack_id?: string
  min?: number //  
  max?: number //  
  sortCards?: string //  
  page?: number //  
  pageCount?: number //  
}

export type CardsResponceType = {
  cards: CardType[]
  packUserId: string
  packName: string
  packPrivate: boolean
  packDeckCover: string
  packCreated: string
  packUpdated: string
  page: number
  pageCount: number
  cardsTotalCount: number
  minGrade: number
  maxGrade: number
  token: string
  tokenDeathTime: number
}

export type CardType = {
  _id: string
  cardsPack_id: string
  user_id: string
  question: string
  answer: string
  grade: number
  shots: number
  questionImg: string
  answerImg: string
  answerVideo: string
  questionVideo: string
  comments: string
  type: string
  rating: number
  more_id: string
  created: string
  updated: string
  __v: number
}

export type NewCardType = {
  card: {
    cardsPack_id?: string
    question?: string //     
    answer?: string //     
    grade?: number // 0..5,  
    shots?: number //  
    answerImg?: string //  
    questionImg?: string //  
    questionVideo?: string //  
    answerVideo?: string //  
  }
}

export type UpdateCardType = {
  card: {
    _id: string
    answer?: string
    question?: string //  
  }
}
